import Tasks from '@/pages/task-page.vue'
import Levels from '@/pages/levels-page.vue'
import Auth from '@/pages/auth-page.vue'

import axios from 'axios'
import store from '@/store/index.js';
import { createRouter, createWebHistory } from 'vue-router'

const routes = [
  { 
    path: '/quiz', 
    component: Tasks,
    beforeEnter: async (to, from, next) => {
      await axios.get(`http://localhost:6506/modules/:${localStorage.login}/:${store.state.modules[localStorage.currentModuleIndex]}/:${localStorage.currentLevelIndex}`)
        .then(res => { store.commit('updateTasks', res.data); next()})
        .catch(err => store.commit('showError', { text: 'Недійсна відповідь сервера' }))
    }
  },
  { 
    path: '/', 
    component: Levels,
    beforeEnter: async (to, from, next) => {
      await axios.get('http://localhost:6506/get-modules:' + localStorage.getItem('login'))
        .then(res => store.commit('updateModules', res.data))
        .catch(err => store.commit('showError', { text: 'Недійсна відповідь сервера' }))
      await axios.get(`http://localhost:6506/modules/:${localStorage.login}/:${store.state.modules[localStorage.currentModuleIndex]}`)
        .then(res => { store.commit('updateLevels', res.data); next()})
        .catch(err => store.commit('showError', { text: 'Недійсна відповідь сервера' }))
    }
  },
  { 
    path: '/auth', 
    name: 'auth',
    component: Auth ,
  }
]

const router = createRouter({ routes, history: createWebHistory() })

router.beforeEach((to,from,next) => {
  if (window.localStorage.isAuthorized == 0 && to.name !== 'auth') router.push('/auth').catch(()=>{})
  else next()
})

export default router;