<template>
<div class="form-wrapper">
  <form @submit.prevent='signIn()'>
    <input type="text" v-model='login' placeholder="Логін" required>
    <input type="text" v-model='password' placeholder="Пароль" required>
    <button type='submit' class='button-plain'>Увійти</button>
  </form>
</div>
</template>

<script>
import axios from 'axios'
import router from '@/router/router'
export default {
  data() { return {
    login: undefined,
    password: undefined
  }},
  methods: {
    async signIn() {
      await axios.post('http://localhost:6506/sign-in', { login: this.login, password: this.password })
        .then(res => {
          this.$store.commit('showSuccess', { text: res.data.message })
          localStorage.setItem('isAuthorized', 1)
          localStorage.setItem('login', this.login)
          localStorage.setItem('currentModuleIndex', 0)
          router.push({ path: '/'})
      }).catch(err => {
          if (err.response) this.$store.commit('showError', { text: err.response.data.message }) 
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.form-wrapper {
  position: fixed;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
form {
  width: min(400px, 90%);
  padding: 60px 15px;
  border-radius: 8px;
  background: white;
  box-shadow: 0 0 10px -2px rgba(0,0,0,.2);
}
input {
  position: relative;
  left: -4px;
  width: 100%;
  display: block;
  margin-bottom: 25px;
}
button {
  width: 100%;
}
</style>