<template>
  <input
    type='radio'
    :name='rowIndex'
    :value='String.fromCharCode(1040 + cellIndex)'
    :class='isCorrect == undefined ? "undefined" : isCorrect == 1 ? "correct" : "incorrect"'
    :checked='modelValue == String.fromCharCode(1040 + cellIndex)'
    :disabled='isCorrect == undefined ? 0 : 1'
    @change="$emit('update:modelValue', $event.target.value)"
  />
</template>

<script>
export default {
  props: ['rowIndex', 'cellIndex', 'isCorrect', 'modelValue']
}
</script>

<style lang = 'scss' scoped>
@import "@/assets/main.scss";
input {
  @include flex-center;
  appearance: none;
  position: relative;
  width: 2em;
  height: 2em;
  border: 2px solid lightgray;
  border-radius: 2px;
  &:hover { border-color: #323232; }
  &:checked {
    &::before, &::after {
      content: "";
      position: absolute;
      background: #323232;
      width: 1.5em;
      height: 2px;
      border-radius: 1px;
    }
    &::before { transform: rotate(45deg); }
    &::after { transform: rotate(-45deg); }
  }
}
.undefined:checked { border-color: #323232 }
.correct:checked { border-color: #2ECC71; border-width: 3px; }
.incorrect:checked { border-color: #E74C3C; border-width: 3px; }
</style>