<template>
  <input type="checkbox" id="side-menu-checkbox">
  <label for="side-menu-checkbox">
    <div class="material-icons">menu</div>
  </label>
  <div class="side-menu">
    <div v-for='i in modules.length' :key='i' @click='gotoModule(i-1)'>{{modules[i-1]}}</div>
  </div>
</template>

<script>
export default {
  computed: {
    modules() { return this.$store.state.modules }
  },
  methods: {
    gotoModule(index) { 
      localStorage.setItem("currentModuleIndex", index) 
      window.location.reload();
    }
  }
}
</script>

<style lang="scss" scoped>
.side-menu {
  width: 100%;
  height: 200px;
  z-index: 1;
  position: fixed;
  left: 0; top: -200px;
  transition: top .5s;
  background: rgba(#323232,.97);
}
#side-menu-checkbox {
  appearance: none;
  &:checked ~ .side-menu {top: 0}
}

</style>