<template >
  <div id='task-title-wrapper'>
    <router-link to="/" id='task-button-back'>
      <span class="material-icons">arrow_back</span>
    </router-link>
    <span class='title'>{{level.name}}</span>
  </div>
  <modal-result
    v-model:isVisible='modalVisible' 
    :ratio='levelRatio'></modal-result>
  <div id='task-body-wrapper'>
    <div id="task-pagination">
      <task-pagination-cell
        v-for='i in level.tasks.length' :key='i'
        :task='i-1' :taskIndex='this.taskIndex'
        :ratio='taskRatios[i-1]'
        @taskGoto='taskGoto'
        >{{i}}</task-pagination-cell>
    </div>
    <div id="task-body">
      <div id="task-text">{{task.text}}</div>
      <div id="task-grid-A">
        <div id="task-items" v-if='task.items'>
          <div class="task-option" v-for='(item, index) in task.items' :key='index'>{{1 + index}} {{item}}</div>
        </div>
        <div id="task-options" v-if='task.options'>
          <div class="task-option" v-for='(option, index) in task.options' :key='index'>{{String.fromCharCode(1040 + index)}} {{option}}</div>
        </div>
        <div id="task-image" v-if='task.image'>
          <img :src='require("@/assets/images/" + task.image)' alt="loading...">
        </div>
      </div>
      <div id="task-grid-B">
        <div id="task-tab">
          <task-table 
            :answer='answers[taskIndex]' 
            :isCorrect='levelCompleted ? taskBools[taskIndex] : undefined' 
            :isInput='level.tasks[taskIndex].options !== undefined'/>
        </div>
        <button-plain @click='taskGoto(this.taskIndex + 1)' :style='`background: ${this.levelCompleted?"lightgray":"#323232"}`'>Далі</button-plain>
        <button-plain v-if='levelCompleted && !levelFinished' @click='levelCheck()'>Перевірити</button-plain>
        <button-plain v-if='levelFinished' @click='modalVisible=true'>Результат</button-plain>
      </div>
    </div>
  </div>
</template>


<script>
import axios from 'axios'
import taskTable from '@/components/task-table.vue'
import taskPaginationCell from '@/components/task-pagination-cell.vue'
export default {
  components: { 
    taskTable, 
    taskPaginationCell, 
  },
  data() { return {
      taskIndex: 0,
      taskRatios: [],
      taskBools: [undefined], //
      levelRatio: undefined,
      levelFinished: false,
      answers: [undefined],
      modalVisible: false,
  }},
  created() {
    this.$nextTick(_ => MathJax.Hub.Queue(["Typeset", MathJax.Hub]));
    if (this.level.answers == undefined)
      this.answers = this.level.tasks.map(a => Array.from(a.key).fill(undefined))
    else {
      this.answers = this.level.answers
      this.levelFinish()
    }
    //this.taskGoto(0)
  },
  computed: {
    levelCompleted() { return !this.answers.flat().includes(undefined) },
    level() { return this.$store.state.level },
    task() { return this.$store.state.level.tasks[this.taskIndex] },
  },
  methods: {
    taskGoto(val) {
      if (val < this.level.tasks.length) {
        this.taskIndex = val;
        this.$nextTick(_ => MathJax.Hub.Queue(["Typeset", MathJax.Hub])); }
    },
    async levelCheck() {
      this.$store.commit("updateAnswers", {index: this.$store.state.levelIndex, body: this.answers})
      await axios.post(`http://localhost:6506/publish-answer`, {
        username: localStorage.login,
        module: this.$store.state.modules[localStorage.currentModuleIndex],
        index: localStorage.currentLevelIndex,
        answers: this.answers,
        score: this.level.levelScore,
        maxscore: this.level.levelScoreMax,
      })
      await axios.post(`http://localhost:6506/publish-score`, {
        username: localStorage.login,
        score: this.level.levelScore,
      })
      this.modalVisible = true;
      this.levelFinish()
    },
    levelFinish() {
      this.levelFinished = true; 
      this.taskBools = this.level.taskBools;
      this.taskRatios = this.level.taskRatios;
      this.levelRatio = this.level.levelRatio;
    }
  },
}
</script>

<style lang = 'scss' scoped>
@import '@/assets/main.scss';

#task-title-wrapper {
  display: block;
  padding-bottom: 20px;
  #task-button-back {
    display: inline-block;
    vertical-align: middle;
  }
}
#task-body-wrapper {
  background: white;
  border-radius: 8px;
  border: 2px solid lightgray
  //box-shadow: 0 -2px 10px 2px rgba(0,0,0,.2);
}
#task-pagination { 
  padding: 12px 40px;
  border-radius: 8px 8px 0 0;
  z-index: 2;
  box-shadow: 0 5px 10px -4px rgba(0,0,0,.6);
}
#task-text { 
  margin-bottom: 20px; 
  line-height: 1.5em; 
}
#task-body {
  padding: 30px 40px 40px 40px;
  border-radius: 0 0 8px 8px;

}
#task-grid-A {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  #task-image { flex-grow: 0; }
  #task-items { flex-grow: 1; }
  #task-options { flex-grow: 1 }
  .task-option { 
    padding: .75em 3ch; 
    line-height: 1.5em;
    &::first-letter {
      font-size: 1.1em;
      font-weight: 500; 
      padding-right: 1ch;
      margin-left: -2ch;
    }
  }
}
#task-grid-B {
  display: flex;
  padding-top: 30px;
  gap: 20px 0;
  @media (max-width:650px) {
    flex-wrap: wrap; 
    align-items: flex-start;
  }
  @media (min-width:650px) {
    align-items: flex-end;
  }
  #task-tab { 
    flex-basis: 100%
  }
}

</style>
