<template>
<div id="wrapper">
  <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 120 120">
    <circle class='circle1' r=50 cx=60 cy=60></circle>
    <circle class='circle2' r=50 cx=60 cy=60 
      :stroke-dasharray='ratio*314 + " 314"' ref='circ'
      :style='`--len: ${ratio*314}`'></circle>
  </svg>
  <span :style='`--perc: ${Math.floor(ratio*100)}`'></span>
</div>
</template>

<script>
export default {
  name: 'progress-bar',
  props: ['ratio'],
}
</script>

<style lang="css" scoped>
@property --num {
  syntax: "<integer>";
  initial-value: 0;
  inherits: false;
}
svg {
  width: 100%;
  height: 100%;
}
#wrapper {
  position: relative;
}
span {
  animation: counter 2s linear forwards;
  counter-reset: num var(--num);
  position: absolute;
  top: 50%; left: 50%;
  transform: translate(-50%, -50%);
}
span::after { 
  content: counter(num); 
  color: white;
  font-weight: bold;

}
@keyframes counter {
  from { --num: 0; }
  to { --num: var(--perc); }
}
circle {
  fill: none; 
  transform: rotate(-90deg); 
  transform-origin: center;
  animation: dash 2s linear forwards;
}
@keyframes dash {
  0% {stroke-dashoffset: var(--len);}
  100% {stroke-dashoffset: 0;}
}
.circle1 {
  stroke: white;
  stroke-width: 5px; 
  stroke-opacity: .24;
}
.circle2 {
  stroke: white;
  stroke-width: 7px; 
  stroke-linecap: round;
}
</style>