import {createStore} from 'vuex'
import axios from 'axios'
import Level from '@/classes.js'

export default createStore({
  state: () => ({ 
    levels: [],
    level: [],
    modules: [],
    modalVisible: false,
    modalColor: undefined,
    modalIcon: undefined,
    modalTitle: undefined,
    modalText: undefined
  }),
  mutations: {
    updateModules(state, payload) { state.modules = payload },
    updateLevels(state, payload) { state.levels = payload },
    updateTasks(state, payload) { state.level = new Level(payload) },
    updateAnswers(state, data) { state.level.answers = data.body },
    showError(state, payload) {
      state.modalVisible = true
      state.modalColor = '#E74C3C'
      state.modalIcon = 'close'
      state.modalTitle = 'Помилка'
      state.modalText = payload.text
    },
    showSuccess(state, payload) {
      state.modalVisible = true
      state.modalColor = '#2ECC71'
      state.modalIcon = 'done'
      state.modalTitle = 'Успіх!'
      state.modalText = payload.text
    },
    hideModal(state) { state.modalVisible = false }
  },
  getters: {
    getLevels(state) { return state.levels },
  },

})