<template>
  <table v-if='isInput' cellspacing=5>
    <tr>
      <th v-for='j in 5' :key='j'>{{ String.fromCharCode(1039 + j) }}</th>
    </tr>
    <tr v-for='i in answer.length' :key='i'>
      <td v-for='j in 5' :key='j'>
        <task-table-cell
        :rowIndex='i-1'
        :cellIndex='j-1'
        :isCorrect='isCorrect == undefined ? undefined : isCorrect[i-1]'
        v-model='answer[i-1]'/>
      </td>
    </tr>
  </table>
  <div v-if='!isInput'>
    <task-table-input
    v-for='i in answer.length' :key='i'
    :isCorrect='isCorrect == undefined ? undefined : isCorrect[i-1]'
    v-model='answer[i-1]'/>
  </div>
</template>

<script>
import taskTableCell from "@/components/task-table-cell.vue";
import taskTableInput from "@/components/task-table-input.vue";
export default {
  components: { taskTableCell, taskTableInput },
  props: ['answer', 'isCorrect', 'isInput'],
};
</script>